
.accordion.accordion-icon {
	.card {
		border: none;

		.card-header {
			background: transparent;
			border-radius: 5px;
			border:1px solid $border-color;
			margin-bottom: 16px;
			padding: 18px 25px;
			box-shadow: 0 4px 6px rgba($dark, 0.05);

			.accordion-title {
				font-size: 16px;
				color: $dark;
				font-weight: 500;
				display: flex;
				align-items: center;

				&:before {
					font-family: "Font Awesome 5 Free";
				    position: absolute;
				    top: 24px;
				    right: 30px;
				    content: "\f077";
				    font-weight: 900;
				    color: $primary;
				    transition: all 0.3s ease-in-out;
				}

				i {
					font-size: 34px;
					color: $primary;
					margin-right: 20px;
				}
			}
			.accordion-title.collapsed {
					&:before {
						content: "\f078";
						color: $dark;
					}
			}
		}
	}
}



#tabs {
	h6.section-title {
		color: $dark;
	}
	.nav-tabs {
		.nav-item.show {
			.nav-link {
				color: $dark;
				background-color: transparent;
				border-color: $primary;
				border-bottom: 4px solid !important;
				font-size: 16px;
				font-weight: 500;
			}
		}
		.nav-link {
			border: 1px solid transparent;
			border-top-left-radius: .25rem;
			border-top-right-radius: .25rem;
			color: $dark;
			font-size: 16px;
			display: flex;
			flex-direction: column;
			align-items: center;
			img {
				width: 50px;
			}
		}
	}
}
.nav-tabs {
	.nav-link.active {
		color: $dark;
		background-color: transparent;
		border-color: $primary;
		border-bottom: 4px solid !important;
		font-size: 16px;
		font-weight: 500;
	}
}

