//* List *//

.list-unstyled i {
    line-height: 2;
}

.list-style-02 .list-unstyled li {
    align-items: center;
}

.list-style-02 .list-unstyled i {
    background: $primary;
    color: $white;
    font-size: 12px;
    align-items: center;
    border-radius: 50%;
    height: 25px;
    width: 25px;
    justify-content: center;
    display: flex;
}
