

.btn {
	padding: 12px 30px;
    font-size: 1rem;
    border-radius: 0;
    box-shadow: none;

    &:focus {
    	box-shadow: none;
    }
    &:active {
    	box-shadow: none;
    }

}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus {
	box-shadow: none;
}


.btn.btn-white {
	background: $white;
	color: $primary;
	font-weight: 500;
	&:hover {
		background: $dark;
		border-color: $dark;
	}
}

.btn.btn-primary {
	background: $primary;
	border-color: $primary;
	color: $white;
	font-weight: 500;
	&:hover {
		background: $dark;
	}
}


.btn.btn-link {
	padding-left: 0;
	padding-right: 0;
	font-weight: 500;
	color: $dark;
	border-bottom: 2px solid transparent;
	border-radius: none !important;

	&:hover {
		color: $primary;
		border-bottom: 2px solid $light;
	}
}

.btn {
	display: inline-block;
	font-weight: 400;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	border: 1px solid transparent;
	// padding: .375rem .75rem;
	// font-size: 1rem;
	// line-height: 1.5;
	// border-radius: 4px;
	-webkit-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
	-o-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out
}



.btn:hover,
.btn:focus {
	text-decoration: none
}

.btn:focus,
.btn.focus {
	outline: 0;
	-webkit-box-shadow: 0 0 0 .2rem rgba(173, 125, 43, 0.25);
	box-shadow: 0 0 0 .2rem rgba(173, 125, 43, 0.25)
}

.btn.disabled,
.btn:disabled {
	opacity: .65
}

.btn:not(:disabled):not(.disabled) {
	cursor: pointer
}

.btn:not(:disabled):not(.disabled):active,
.btn:not(:disabled):not(.disabled).active {
	background-image: none
}

a.btn.disabled,
fieldset:disabled a.btn {
	pointer-events: none
}

.btn.btn-brown2 {
	background-color: $primary;
	color: $white;
	position: relative;
	overflow: hidden
}

.btn.btn-brown2::before {
	content: "rtl";
	display: block;
	background-color: $white;
	width: 100%;
	height: 100%;
	margin: auto;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	-webkit-transition: -webkit-transform 0.3s cubic-bezier(0.38, 0.32, 0.36, 0.98) 0s;
	transition: -webkit-transform 0.3s cubic-bezier(0.38, 0.32, 0.36, 0.98) 0s;
	-o-transition: transform 0.3s cubic-bezier(0.38, 0.32, 0.36, 0.98) 0s;
	transition: transform 0.3s cubic-bezier(0.38, 0.32, 0.36, 0.98) 0s;
	transition: transform 0.3s cubic-bezier(0.38, 0.32, 0.36, 0.98) 0s, -webkit-transform 0.3s cubic-bezier(0.38, 0.32, 0.36, 0.98) 0s;
	-webkit-transform: scaleX(0);
	-ms-transform: scaleX(0);
	transform: scaleX(0);
	-webkit-transform-origin: right center;
	-ms-transform-origin: right center;
	transform-origin: right center
}

.btn.btn-brown2 span,
.btn.btn-brown2 .icon {
	position: relative;
	z-index: 2;
	-webkit-transition: inherit;
	-o-transition: inherit;
	transition: inherit
}

.btn.btn-brown2:hover::before {
	-webkit-transform: scale(1);
	-ms-transform: scale(1);
	transform: scale(1);
	-webkit-transform-origin: left center;
	-ms-transform-origin: left center;
	transform-origin: left center
}

.btn.btn-brown2:hover span,
.btn.btn-brown2:hover .icon {
	color: $primary;
}


.btn.btn-arrow {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	text-align: left;
	max-width: 340px;
	padding: 24px 35px 19px 35px
}

@media (max-width:575px) {
	.btn.btn-arrow {
		padding: 20px 35px 17px 35px;
		max-width: 280px;
		font-size: .8125rem
	}

}

.btn.btn-arrow span {
	display: block;
	margin-right: 20px
}

.btn.btn-arrow .icon {
	font-size: 6px
}

.btn.btn-arrow:hover .icon,
.btn.btn-arrow:focus .icon,
.btn.btn-arrow:active .icon {
	-webkit-transform: translateX(15px);
	-ms-transform: translateX(15px);
	transform: translateX(15px)
}
