
.blog-post {
	box-shadow: 0 6px 16px rgba($black, 0.06);
	.blog-image{
	position: relative;
	.blog-post-video {
		justify-content: end;
		position: absolute;
		line-height: 50px;
		font-size: 20px;
		right: 20px;
		box-shadow: 0 6px 16px rgba($primary, 0.2);
		&:hover {
			background: $dark;
		}
	}
	}
	 .blog-caption {
	 	padding: 34px;
	 	.blog-meta {
	 		display: flex;
	 		li {
	 			margin-right: 8px;
	 		}
	 	}
	 }
}

.other-info li a {
	color: $body-text;
	i{
		color: $primary;
	}
	&:hover {
		color: $primary;
	}
}


/* Blog Overlay Style */

.blog-post.blog-overlay-style .blog-caption {
    position: absolute;
    z-index: 9;
    bottom: -110px;
    margin: 0 15px;
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.06);
}


/* Blog style 3 */

.blog-post-style-03 .blog-post-img img {
    height: 100px;
    border-radius: 50%;
    width: 100px;
    margin-bottom: 20px;
}

.blog-post-style-03 .blog-post-contint {
    border-left: 1px solid #eee;
    padding-left: 50px;
}

