
.social {
	display: flex;
	li {
		margin-right: 10px;
		display: block;
		:last-child {
			margin-right: 0;
		}
		a {
			font-size: 18px;
		}

	}
}

li.facebook {
	a {
		color: #3b5998;
	}
}
li.instagram {
	a {
		color: #dd2a7b;
	}
}
li.twitter {
	a {
		color: #00acee;
	}
}
li.pinterest {
	a {
		color: #c8232c;
	}
}
li.rss {
	a {
		color: #ee802f;
	}
}
