

 	.form-control{
 		border-radius: 0;
 		&:focus{
 			box-shadow: none;
 			border-color: $primary;
 		}
 	}


 .form-light {
 	.form-control {
 		background: rgba($white, 0.04);
 		border-color: rgba($white, 0.04);
 		border-radius: 0;
 	}
 }

 .form-control-lg {
    height: 80px;
    padding: 40px;
    font-size: 14px;
 }



 .newsletter-big {
 	.form-control{
 		padding: 30px;
 		border-radius: 0;
 	}
 	.btn {
 		padding: 19px 20px;
 		border-radius: 0;
 		border: none;
 	}
 }
