html {
	height: 100%;
	margin: 0;
	padding: 0;
}
body {
	height: 100%;
	margin: 0;
	padding: 0;
	font-size: 14px;
	line-height: 24px;
	font-family: $main-font;
	display: flex;
	flex-direction: column;
	color: $body-text;
	overflow-x: hidden;
}
a {
	text-decoration: none !important;
	outline: none !important;
	transition: all 0.4s ease-in-out;
	color: $primary;
	&:hover {
		outline: none !important;
		text-decoration: none !important;
		color: $primary;
	}
	&:focus {
		outline: none !important;
		text-decoration: none !important;
		color: $primary;
	}
	&:active {
		outline: none !important;
		text-decoration: none !important;
		color: $primary;
	}
}

p {
	line-height: 1.6;
}

ul {
	list-style: none;
	padding: 0;
}

small {
	font-size: 80%;
	font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
	font-family: $hedding-font;

}

h1, .h1 {
	font-size: 44px;
	line-height: 45px;
	font-weight: 700;
	color: $dark;
	a {
		color: inherit;
	}
}
h2, .h2 {
	font-size: 34px;
	line-height: 45px;
	font-weight: 500;
	color: $dark;
	a {
		color: inherit;
	}
}
h3, .h3 {
	font-size: 30px;
	line-height: 1.2;
	font-weight: 500;
	color: $dark;
	a {
		color: inherit;
	}
}
h4, .h4 {
	font-size: 26px;
	line-height: 1.3;
	font-weight: 500;
	color: $dark;
	a {
		color: inherit;
	}
}
h5, .h5 {
	font-size: 20px;
	line-height: 1.3;
	font-weight: 500;
	color: $dark;
	a {
		color: inherit;
	}
}
h6, .h6 {
	font-size: 18px;
	line-height: 1.3;
	font-weight: 500;
	color: $dark;
	a {
		color: inherit;
	}
}
.separator {
	border-bottom: 1px solid $border-color;
}

@media (min-width: 1300px){
	.container {
		max-width: 1300px;
	}
}

//* Inner Header *//
.inner-header {
	padding: 180px 0;
	.breadcrumb {
		display: inline-flex;
		background: transparent;
	}
}


//* Portfolio *//
.portfolio-item {
	position: relative;
	overflow: hidden;
	.portfolio-image {
		position: relative;
		transition: all 0.4s ease-in-out;
		&:before {
			position: absolute;
			width: 100%;
			height: 100%;
			content: "";
			left: 0;
			top: 0;
			background: rgba($black, 0.5);
		}
	}
	.portfolio-caption {
		padding: 40px;
		position: absolute;
		top: 0;
		left: 0;
		.portfolio-category {
			display: flex;
			li {
				margin-right: 10px;
				a {
					color: $white;
					&:hover {
						color: $primary;
					}
				}
			}
		}
		.portfolio-title {
			color: $white;
			&:hover {
				color: $primary;
			}
		}
	}
	.portfolio-date {
		padding: 40px;
		position: absolute;
		bottom: 0;
		right: 0;
		a {
			color: $white;
			&:hover {
				color: $primary;
			}
		}
	}
	&:hover {
		.portfolio-image {
			transform: scale(1.05);
		}
	}
}



/* video */
.video-image .video-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}



/* pricing table */
.pricing-table .table thead th {
	border: none;
}

.pricing-table .table-hover tbody tr:hover {
    background-color: rgb(255, 255, 255);
}

.pricing-table .table tbody td i {
    background: #ffffff;
    height: 45px;
    width: 45px;
    line-height: 45px !important;
    border-radius: 50%;
    border: 1px solid limegreen;
}

.pricing-table .table td, .pricing-table .table th {
    padding: 20px 0;
    vertical-align: middle;
}

.pricing-table .table td h3 {
	margin-bottom: 0;
}



