  position: absolute;
  z-index: 9;
  bottom: -110px;
  margin: 0 15px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.06); }

/* Blog style 3 */
.blog-post-style-03 .blog-post-img img {
  height: 100px;
  border-radius: 50%;
  width: 100px;
  margin-bottom: 20px; }

.blog-post-style-03 .blog-post-contint {
  border-left: 1px solid #eee;
  padding-left: 50px; }

html {
  height: 100%;
  margin: 0;
  padding: 0; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  font-family: "Poppins", sans-serif;
  display: flex;
  flex-direction: column;
  color: #898b96;
  overflow-x: hidden; }

a {
  text-decoration: none !important;
  outline: none !important;
  transition: all 0.4s ease-in-out;
  color: #2A3CB7; }
  a:hover {
    outline: none !important;
    text-decoration: none !important;
    color: #2A3CB7; }
  a:focus {
    outline: none !important;
    text-decoration: none !important;
    color: #2A3CB7; }
  a:active {
    outline: none !important;
    text-decoration: none !important;
    color: #2A3CB7; }

p {
  line-height: 1.6; }

ul {
  list-style: none;
  padding: 0; }

small {
  font-size: 80%;
  font-weight: 400; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Jost", sans-serif; }

h1, .h1 {
  font-size: 44px;
  line-height: 45px;
  font-weight: 700;
  color: #000; }
  h1 a, .h1 a {
    color: inherit; }

h2, .h2 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 500;
  color: #000; }
  h2 a, .h2 a {
    color: inherit; }

h3, .h3 {
  font-size: 30px;
  line-height: 1.2;
  font-weight: 500;
  color: #000; }
  h3 a, .h3 a {
    color: inherit; }

h4, .h4 {
  font-size: 26px;
  line-height: 1.3;
  font-weight: 500;
  color: #000; }
  h4 a, .h4 a {
    color: inherit; }

h5, .h5 {
  font-size: 20px;
  line-height: 1.3;
  font-weight: 500;
  color: #000; }
  h5 a, .h5 a {
    color: inherit; }

h6, .h6 {
  font-size: 18px;
  line-height: 1.3;
  font-weight: 500;
  color: #000; }
  h6 a, .h6 a {
    color: inherit; }

.separator {
  border-bottom: 1px solid #e5e5e5; }

@media (min-width: 1300px) {
  .container {
    max-width: 1300px; } }

.inner-header {
  padding: 180px 0; }
  .inner-header .breadcrumb {
    display: inline-flex;
    background: transparent; }

.portfolio-item {
  position: relative;
  overflow: hidden; }
  .portfolio-item .portfolio-image {
    position: relative;
    transition: all 0.4s ease-in-out; }
    .portfolio-item .portfolio-image:before {
      position: absolute;
      width: 100%;
      height: 100%;
      content: "";
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.5); }
  .portfolio-item .portfolio-caption {
    padding: 40px;
    position: absolute;
    top: 0;
    left: 0; }
    .portfolio-item .portfolio-caption .portfolio-category {
      display: flex; }
      .portfolio-item .portfolio-caption .portfolio-category li {
        margin-right: 10px; }
        .portfolio-item .portfolio-caption .portfolio-category li a {
          color: #ffffff; }
          .portfolio-item .portfolio-caption .portfolio-category li a:hover {
            color: #2A3CB7; }
    .portfolio-item .portfolio-caption .portfolio-title {
      color: #ffffff; }
      .portfolio-item .portfolio-caption .portfolio-title:hover {
        color: #2A3CB7; }
  .portfolio-item .portfolio-date {
    padding: 40px;
    position: absolute;
    bottom: 0;
    right: 0; }
    .portfolio-item .portfolio-date a {
      color: #ffffff; }
      .portfolio-item .portfolio-date a:hover {
        color: #2A3CB7; }
  .portfolio-item:hover .portfolio-image {
    transform: scale(1.05); }

/* video */
.video-image .video-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

/* pricing table */
.pricing-table .table thead th {
  border: none; }

.pricing-table .table-hover tbody tr:hover {
  background-color: white; }

.pricing-table .table tbody td i {
  background: #ffffff;
  height: 45px;
  width: 45px;
  line-height: 45px !important;
  border-radius: 50%;
  border: 1px solid limegreen; }

.pricing-table .table td, .pricing-table .table th {
  padding: 20px 0;
  vertical-align: middle; }

.pricing-table .table td h3 {
  margin-bottom: 0; }
