//* Testimonial *//

.blockquote:before {
    content: "\f10d";
    font-size: 26px;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    position: absolute;
    top: 20px;
    left: 30px;
    color: #eeeeee;
}

.blockquote:after {
    content: "\f10e";
    font-size: 26px;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    position: absolute;
    bottom: 20px;
    right: 170px;
    color: #eeeeee;
}
