//* Portfolio *//

.portfolio-item-02 {
	position: relative;
	overflow: hidden;
	.portfolio-image {
		position: relative;
		transition: all 0.4s ease-in-out;
		&:before {
			position: absolute;
			width: 100%;
			height: 100%;
			content: "";
			left: 0;
			top: 0;
			background: rgba($black, 0.5);
		}
	}
	.portfolio-caption {
		padding: 40px;
		position: absolute;
		bottom: 0;
		left: 0;
		.portfolio-category {
			display: flex;
			li {
				margin-right: 10px;
				a {
					color: $white;
					&:hover {
						color: $primary;
					}
				}
			}
		}
		.portfolio-title {
			color: $white;
			&:hover {
				color: $primary;
			}
		}
	}
	.portfolio-popup {
		padding: 40px;
		position: absolute;
		top: 0;
		left: 0;
		line-height: 50px;
		font-size: 22px;
		a {
			color: $white;
			&:hover {
				color: $primary;
			}
		}
	}
	&:hover {
		.portfolio-image {
			transform: scale(1.05);
		}
	}
}

.filters-group {
	display: inline-flex;
	margin-bottom: 30px;
	.button {
		padding: 0px 3px;
		margin: 0 20px;
		border: none;
		border: 2px solid transparent;
		background: transparent;
		font-weight: 500;
		transition: all .4s ease-in-out;
		outline: none;
		border-radius: 0;
	}
	.button.is-checked {
		color: $primary;
		border-bottom-color: $primary;
	}
}
.grid.column-3 {
	.grid-item {
		width: 33.333%;
	}
}
.portfolio-item-03 {
	position: relative;
	margin: 10px;
	.portfolio-item-image {
		position: relative;
		text-align: center;
		&:before {
			position: absolute;
			content: "";
			width: 100%;
			background: rgba($dark, 0.6);
			width: 100%;
			height: 100%;
			opacity: 0;
			transition: all .4s ease-in-out;
		}
	}
	.portfolio-item-caption {
		position: relative;
		padding: 30px 0;
		width: 100%;
		a {
			font-size: 18px;
			font-weight: 500;
			display: block;
			color: $dark;
			&:hover {
				color: $primary;
			}
		}
	}
	.portfolio-item-category {
		li {
			display: inline-block;
			margin-right: 4px;
			a {
				font-size: 12px;
				border-bottom: 2px solid $border-color;
				&:hover {
					border-color: $primary;
			  }
		}
	}
	.portfolio-item-hover {
		a {
			width: 50px;
			height: 50px;
			line-height: 50px;
			background: $white;
			border-radius: 50%;
			color: $primary;
			display: inline-block;
			text-align: center;
		}
	}
	&:hover {
		.portfolio-item-image {
			&:before {
				opacity: 1;
			}
		}
		.portfolio-item-hover {
			top: 50%;
			opacity: 1;
		}
	}
}
}
	.portfolio-item-03 {
		.portfolio-item-hover {
			position: absolute;
			z-index: 1;
			top: 40%;
			transform: translateY(-50%);
			left: 0;
			right: 0;
			opacity: 0;
			transition: all .4s ease-in-out;
		}
	}
.portfolio-item {
	.portfolio-caption-bottom {
		bottom: 10px;
		top: inherit !important;
		padding: 0 !important;
		left: 30px !important;
	}
}
.portfolio-style-02 {
	.portfolio-item {
		.portfolio-caption {
			.portfolio-title {
				color: #323232;
			}
			.portfolio-category {
				li {
					a {
						color: #323232;
					}
				}
			}
		}
	}
}
