//* Pricing Table *//

.pricing-table {
	.plan-features {
		display: flex;
		border-bottom: 1px solid $border-color;
		margin-bottom: 0;
		align-items: center;
		li {
			padding: 15px;
			width: 25%;
			text-align: center;
			color: $dark;
			h5 {
				margin-bottom: 0;
			}
			i.fa-check {
				color: $dark;
				height: 45px;
				width: 45px;
				line-height: 45px !important;
				border-radius: 50%;
				border: 1px solid limegreen;
			}
		}
		&:hover {
			background-color: rgb(255, 255, 255);
		}
	}
}
